"use client";

import { useAuth } from '@/context/AuthContext';
import { useRouter } from 'next/navigation';
import { useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from '@/components/ui/accordion';
import { Badge } from '@/components/ui/badge';

interface ChitFund {
  id: string;
  name: string;
  totalAmount: number;
  monthlyContribution: number;
  duration: number;
  membersCount: number;
  startDate: string;
  status: 'active' | 'completed' | 'pending';
  nextPaymentDate: string;
  myPosition: number;
}

interface Payment {
  id: string;
  chitFundId: string;
  month: number;
  amount: number;
  dueDate: string;
  paidDate?: string;
  status: 'paid' | 'pending' | 'overdue';
}

const calculatorSchema = z.object({
  totalAmount: z.number().min(1000, 'Total amount must be at least ₹1,000'),
  duration: z.number().min(6, 'Duration must be at least 6 months').max(60, 'Duration cannot exceed 60 months'),
  membersCount: z.number().min(5, 'Minimum 5 members required').max(50, 'Maximum 50 members allowed'),
});

type CalculatorFormData = z.infer<typeof calculatorSchema>;

export default function ChitFundPage() {
  const { user, isLoading } = useAuth();
  const router = useRouter();
  const [chitFunds] = useState<ChitFund[]>([
    {
      id: '1',
      name: 'Monthly Savings Group A',
      totalAmount: 100000,
      monthlyContribution: 5000,
      duration: 20,
      membersCount: 20,
      startDate: '2024-01-01',
      status: 'active',
      nextPaymentDate: '2024-12-15',
      myPosition: 8,
    },
    {
      id: '2',
      name: 'Weekly Business Fund',
      totalAmount: 50000,
      monthlyContribution: 2500,
      duration: 20,
      membersCount: 20,
      startDate: '2024-06-01',
      status: 'active',
      nextPaymentDate: '2024-12-12',
      myPosition: 15,
    },
    {
      id: '3',
      name: 'Emergency Fund Group',
      totalAmount: 200000,
      monthlyContribution: 10000,
      duration: 20,
      membersCount: 20,
      startDate: '2023-01-01',
      status: 'completed',
      nextPaymentDate: '',
      myPosition: 5,
    },
  ]);

  const [payments] = useState<Payment[]>([
    {
      id: '1',
      chitFundId: '1',
      month: 11,
      amount: 5000,
      dueDate: '2024-12-15',
      status: 'pending',
    },
    {
      id: '2',
      chitFundId: '2',
      month: 6,
      amount: 2500,
      dueDate: '2024-12-12',
      status: 'pending',
    },
    {
      id: '3',
      chitFundId: '1',
      month: 10,
      amount: 5000,
      dueDate: '2024-11-15',
      paidDate: '2024-11-14',
      status: 'paid',
    },
  ]);

  const [calculatorResult, setCalculatorResult] = useState<{
    monthlyContribution: number;
    totalContribution: number;
    estimatedReturn: number;
  } | null>(null);

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm<CalculatorFormData>({
    resolver: zodResolver(calculatorSchema),
  });

  useEffect(() => {
    if (!isLoading && !user) {
      router.push('/login');
    }
  }, [user, isLoading, router]);

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <div className="text-center">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-black mx-auto mb-4"></div>
          <p className="text-gray-600">Loading...</p>
        </div>
      </div>
    );
  }

  if (!user) {
    return null;
  }

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat('en-IN', {
      style: 'currency',
      currency: 'INR',
    }).format(amount);
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'active':
        return 'bg-green-100 text-green-800';
      case 'completed':
        return 'bg-blue-100 text-blue-800';
      case 'pending':
        return 'bg-yellow-100 text-yellow-800';
      case 'paid':
        return 'bg-green-100 text-green-800';
      case 'overdue':
        return 'bg-red-100 text-red-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  };

  const onCalculate = (data: CalculatorFormData) => {
    const monthlyContribution = data.totalAmount / data.membersCount;
    const totalContribution = monthlyContribution * data.duration;
    const estimatedReturn = data.totalAmount * 0.85; // Assuming 15% deduction for management

    setCalculatorResult({
      monthlyContribution,
      totalContribution,
      estimatedReturn,
    });
  };

  const activeChitFunds = chitFunds.filter(cf => cf.status === 'active');
  const pendingPayments = payments.filter(p => p.status === 'pending');

  return (
    <div className="max-w-7xl mx-auto">
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-gray-900 mb-2">
          Chit Fund Management
        </h1>
        <p className="text-gray-600">
          Monitor your chit fund contributions, schedules, and calculate returns.
        </p>
      </div>

      {/* Summary Cards */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
        <Card>
          <CardHeader className="pb-2">
            <CardDescription>Active Chit Funds</CardDescription>
            <CardTitle className="text-2xl font-bold text-blue-600">
              {activeChitFunds.length}
            </CardTitle>
          </CardHeader>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardDescription>Pending Payments</CardDescription>
            <CardTitle className="text-2xl font-bold text-orange-600">
              {pendingPayments.length}
            </CardTitle>
          </CardHeader>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardDescription>Monthly Commitment</CardDescription>
            <CardTitle className="text-2xl font-bold text-purple-600">
              {formatCurrency(activeChitFunds.reduce((sum, cf) => sum + cf.monthlyContribution, 0))}
            </CardTitle>
          </CardHeader>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardDescription>Total Investment</CardDescription>
            <CardTitle className="text-2xl font-bold text-green-600">
              {formatCurrency(activeChitFunds.reduce((sum, cf) => sum + cf.totalAmount, 0))}
            </CardTitle>
          </CardHeader>
        </Card>
      </div>

      <Tabs defaultValue="overview" className="space-y-6">
        <TabsList>
          <TabsTrigger value="overview">Overview</TabsTrigger>
          <TabsTrigger value="payments">Payments</TabsTrigger>
          <TabsTrigger value="calculator">Calculator</TabsTrigger>
        </TabsList>

        <TabsContent value="overview">
          <div className="space-y-6">
            <Card>
              <CardHeader>
                <CardTitle>My Chit Funds</CardTitle>
                <CardDescription>
                  Overview of all your chit fund participations
                </CardDescription>
              </CardHeader>
              <CardContent>
                <Accordion type="single" collapsible className="space-y-4">
                  {chitFunds.map((chitFund) => (
                    <AccordionItem key={chitFund.id} value={chitFund.id}>
                      <AccordionTrigger className="hover:no-underline">
                        <div className="flex items-center justify-between w-full pr-4">
                          <div className="flex items-center space-x-4">
                            <div>
                              <h3 className="font-semibold text-left">{chitFund.name}</h3>
                              <p className="text-sm text-gray-500">
                                {formatCurrency(chitFund.monthlyContribution)}/month • {chitFund.membersCount} members
                              </p>
                            </div>
                          </div>
                          <Badge className={getStatusColor(chitFund.status)}>
                            {chitFund.status}
                          </Badge>
                        </div>
                      </AccordionTrigger>
                      <AccordionContent>
                        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 pt-4">
                          <div>
                            <p className="text-sm text-gray-500">Total Amount</p>
                            <p className="font-semibold">{formatCurrency(chitFund.totalAmount)}</p>
                          </div>
                          <div>
                            <p className="text-sm text-gray-500">Duration</p>
                            <p className="font-semibold">{chitFund.duration} months</p>
                          </div>
                          <div>
                            <p className="text-sm text-gray-500">My Position</p>
                            <p className="font-semibold">#{chitFund.myPosition}</p>
                          </div>
                          <div>
                            <p className="text-sm text-gray-500">Start Date</p>
                            <p className="font-semibold">
                              {new Date(chitFund.startDate).toLocaleDateString()}
                            </p>
                          </div>
                          {chitFund.nextPaymentDate && (
                            <div className="md:col-span-2">
                              <p className="text-sm text-gray-500">Next Payment</p>
                              <p className="font-semibold text-orange-600">
                                {new Date(chitFund.nextPaymentDate).toLocaleDateString()}
                              </p>
                            </div>
                          )}
                        </div>
                      </AccordionContent>
                    </AccordionItem>
                  ))}
                </Accordion>
              </CardContent>
            </Card>
          </div>
        </TabsContent>

        <TabsContent value="payments">
          <Card>
            <CardHeader>
              <CardTitle>Payment Schedule</CardTitle>
              <CardDescription>
                Track your chit fund payment history and upcoming dues
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                {payments.map((payment) => {
                  const chitFund = chitFunds.find(cf => cf.id === payment.chitFundId);
                  return (
                    <div
                      key={payment.id}
                      className="flex items-center justify-between p-4 border rounded-lg hover:bg-gray-50"
                    >
                      <div className="flex-1">
                        <div className="flex items-center space-x-3">
                          <div className={`w-3 h-3 rounded-full ${
                            payment.status === 'paid' ? 'bg-green-500' : 
                            payment.status === 'overdue' ? 'bg-red-500' : 'bg-orange-500'
                          }`}></div>
                          <div>
                            <p className="font-medium">{chitFund?.name}</p>
                            <p className="text-sm text-gray-500">
                              Month {payment.month} • Due: {new Date(payment.dueDate).toLocaleDateString()}
                              {payment.paidDate && ` • Paid: ${new Date(payment.paidDate).toLocaleDateString()}`}
                            </p>
                          </div>
                        </div>
                      </div>
                      <div className="flex items-center space-x-4">
                        <div className="text-right">
                          <p className="font-bold">{formatCurrency(payment.amount)}</p>
                          <Badge className={getStatusColor(payment.status)}>
                            {payment.status}
                          </Badge>
                        </div>
                        {payment.status === 'pending' && (
                          <Button size="sm">Pay Now</Button>
                        )}
                      </div>
                    </div>
                  );
                })}
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="calculator">
          <div className="grid md:grid-cols-2 gap-6">
            <Card>
              <CardHeader>
                <CardTitle>Chit Fund Calculator</CardTitle>
                <CardDescription>
                  Calculate monthly contributions and estimated returns
                </CardDescription>
              </CardHeader>
              <CardContent>
                <form onSubmit={handleSubmit(onCalculate)} className="space-y-4">
                  <div className="space-y-2">
                    <Label htmlFor="totalAmount">Total Chit Fund Amount (₹)</Label>
                    <Input
                      id="totalAmount"
                      type="number"
                      placeholder="100000"
                      {...register('totalAmount', { valueAsNumber: true })}
                      className={errors.totalAmount ? 'border-red-500' : ''}
                    />
                    {errors.totalAmount && (
                      <p className="text-sm text-red-500">{errors.totalAmount.message}</p>
                    )}
                  </div>

                  <div className="space-y-2">
                    <Label htmlFor="duration">Duration (months)</Label>
                    <Input
                      id="duration"
                      type="number"
                      placeholder="20"
                      {...register('duration', { valueAsNumber: true })}
                      className={errors.duration ? 'border-red-500' : ''}
                    />
                    {errors.duration && (
                      <p className="text-sm text-red-500">{errors.duration.message}</p>
                    )}
                  </div>

                  <div className="space-y-2">
                    <Label htmlFor="membersCount">Number of Members</Label>
                    <Input
                      id="membersCount"
                      type="number"
                      placeholder="20"
                      {...register('membersCount', { valueAsNumber: true })}
                      className={errors.membersCount ? 'border-red-500' : ''}
                    />
                    {errors.membersCount && (
                      <p className="text-sm text-red-500">{errors.membersCount.message}</p>
                    )}
                  </div>

                  <Button type="submit" className="w-full">
                    Calculate
                  </Button>
                </form>
              </CardContent>
            </Card>

            {calculatorResult && (
              <Card>
                <CardHeader>
                  <CardTitle>Calculation Results</CardTitle>
                  <CardDescription>
                    Based on your input parameters
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <div className="space-y-4">
                    <div className="p-4 bg-blue-50 rounded-lg">
                      <p className="text-sm text-blue-600 font-medium">Monthly Contribution</p>
                      <p className="text-2xl font-bold text-blue-800">
                        {formatCurrency(calculatorResult.monthlyContribution)}
                      </p>
                    </div>

                    <div className="p-4 bg-purple-50 rounded-lg">
                      <p className="text-sm text-purple-600 font-medium">Total Contribution</p>
                      <p className="text-2xl font-bold text-purple-800">
                        {formatCurrency(calculatorResult.totalContribution)}
                      </p>
                    </div>

                    <div className="p-4 bg-green-50 rounded-lg">
                      <p className="text-sm text-green-600 font-medium">Estimated Return</p>
                      <p className="text-2xl font-bold text-green-800">
                        {formatCurrency(calculatorResult.estimatedReturn)}
                      </p>
                    </div>

                    <div className="text-xs text-gray-500 mt-4">
                      * Calculations are estimates and actual returns may vary based on auction results and management fees.
                    </div>
                  </div>
                </CardContent>
              </Card>
            )}
          </div>
        </TabsContent>
      </Tabs>
    </div>
  );
}
